body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.raw{display:flex;justify-content:center}.login-container{margin-top:2%}.login-form-1{border-radius:5px;box-shadow:0 5px 8px 0 rgba(0,0,0,0.2),0 9px 26px 0 rgba(0,0,0,0.19);padding:5%}.login-form-1 h3{text-align:center;color:#333}.login-form-2{border-radius:5px;box-shadow:0 5px 8px 0 rgba(0,0,0,0.2),0 9px 26px 0 rgba(0,0,0,0.19);padding:5%;background:#0062cc}.login-form-2 h3{text-align:center;color:#fff}.login-container form{padding:10%}.btnSubmit{width:50%;border-radius:1rem;padding:1.5%;border:none;cursor:pointer}.login-form-1 .btnSubmit{color:#fff;background-color:#0062cc}.login-form-2 .btnSubmit{background-color:#fff;color:#0062cc}.login-form-2 .ForgetPwd{color:#fff;font-weight:600;text-decoration:none}.login-form-1 .ForgetPwd{color:#0062cc;font-weight:600;text-decoration:none}.link{color:black}.link:hover{color:black}.calendar-screen{display:flex;height:100vh;flex-flow:column}.rbc-calendar{height:100%}.rbc-event{background:none}.ReactModalPortal>div{opacity:0}.ReactModalPortal .ReactModal__Overlay{align-items:center;display:flex;justify-content:center;transition:opacity .2s ease-in-out;z-index:999}.modal-fondo{background-color:rgba(0,0,0,0.3);bottom:0;left:0;right:0;top:0;position:fixed}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:0}.modal{background:white;border-radius:5px;color:#333;display:inline;max-height:620px;max-width:500px;outline:none;padding:10px}@media screen and (max-device-width: 400px){.btn{width:300px}h3{font-size:small}h6{font-size:smaller}}
